/*
    POST, GET, PUT calls defined
 */

function postCall(url, mydata) {
    return new Promise((resolve, reject) => {
        let callingUrl = baseUrl + url;
        resolve(axios.post(callingUrl, mydata)
            .then(response => {
                return response;
            }));
    });
}


function authenticatedGetCall(url, header) {
    const requestHeader = { headers: { "Authorization": "Bearer " + apiConfig.authenticationToken } };

    return new Promise((resolve, reject) => {
        let callingUrl = baseUrl + url;
        resolve(axios.get(callingUrl, requestHeader)
            .then(response => {
                return response;
            }));
    });
}


function putCall(url, updateOps) {
    return new Promise((resolve, reject) => {
        let callingUrl = baseUrl + url;
        resolve(axios.put(callingUrl,updateOps)
            .then(response => {
                return response;
            }));
    });
}